import Vue from "vue";
import Router from "vue-router";

const index = resolve => require(["@/views/index"], resolve);
const home = resolve => require(["@/views/home"], resolve);
const merchant = resolve => require(["@/views/merchant"], resolve);
const usercenter = resolve => require(["@/views/usercenter"], resolve);
const classifyDetails = resolve => require(["@/views/classifyDetails"], resolve);
const classify = resolve => require(["@/views/classify"], resolve);
const cash = resolve => require(["@/views/cash"], resolve);
const funding = resolve => require(["@/views/funding"], resolve);
const fundetails = resolve => require(["@/views/fundetails"], resolve);
const notice = resolve => require(["@/views/notice"], resolve);
const recharge = resolve => require(["@/views/recharge"], resolve);
const detail = resolve => require(["@/views/detail"], resolve);
const orderConfirm = resolve => require(["@/views/orderConfirm"], resolve);
const bill = resolve => require(["@/views/bill"], resolve);
const order = resolve => require(["@/views/order"], resolve);
const orderDetials = resolve => require(["@/views/orderDetials"], resolve);
const extension = resolve => require(["@/views/extension"], resolve);
const addressList = resolve => require(["@/views/addressList"], resolve);
const editaddress = resolve => require(["@/views/editaddress"], resolve);
const apply = resolve => require(["@/views/apply"], resolve);
const merchantDetails = resolve => require(["@/views/merchantDetails"], resolve);
const success = resolve => require(["@/views/successed"], resolve);
const nosuccess = resolve => require(["@/views/nosuccessed"], resolve);

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: 'index',
      component: index,
      redirect: '/home',
      children: [
        {path: '/home', name: 'home', meta: {title: '首页', keepalive: false}, component: home},
        {path: '/merchant', name: 'merchant', meta: {title: '联盟商家', keepalive: true}, component: merchant},
        {path: '/usercenter', name: 'usercenter', meta: {title: '个人中心', keepalive: true}, component: usercenter},
      ]
    },
    {path: '/classifyDetails',name: 'classifyDetails',meta: {title: '分类详情', keepalive: false},component: classifyDetails},
    {path: '/classify', name: 'classify', meta: {title: '分类', keepalive: true}, component: classify},
    {path: '/cash', name: 'cash', meta: {title: '元宝换购', keepalive: true}, component: cash},
    {path: '/recharge', name: 'recharge', meta: {title: '会员充值', keepalive: true}, component: recharge},
    {path: '/funding', name: 'funding', meta: {title: '项目众筹', keepalive: true}, component: funding},
    {path: '/fundetails', name: 'fundetails', meta: {title: '众筹详情', keepalive: false}, component: fundetails},
    {path: '/notice', name: 'notice', meta: {title: '公告', keepalive: true}, component: notice},
    {path: '/detail', name: 'detail', meta: {title: '产品详情', keepalive: false}, component: detail},
    {path: '/orderConfirm', name: 'orderConfirm', meta: {title: '确定订单', keepalive: false}, component: orderConfirm},
    {path: '/bill', name: 'bill', meta: {title: '消费明细', keepalive: true}, component: bill},
    {path: '/order', name: 'order', meta: {title: '订单列表', keepalive: true}, component: order},
    {path: '/orderDetials', name: 'orderDetials', meta: {title: '订单详情', keepalive: false}, component: orderDetials},
    {path: '/extension', name: 'extension', meta: {title: '推广中心', keepalive: true}, component: extension},
    {path: '/addressList', name: 'addressList', meta: {title: '管理地址', keepalive: false}, component: addressList},
    {path: '/editaddress', name: 'editaddress', meta: {title: '地址详情', keepalive: false}, component: editaddress},
    {path: '/apply', name: 'apply', meta: {title: '联盟商家申请', keepalive: false}, component: apply},
    {path: '/merchantDetails', name: 'merchantDetails', meta: {title: '联盟商家详情', keepalive: false}, component: merchantDetails},
    {path: '/success', name: 'success', meta: {title: '支付成功', keepalive: false}, component: success},
    {path: '/nosuccess', name: 'nosuccess', meta: {title: '支付失败', keepalive: false}, component: nosuccess},
  ]
});
