import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    orderC:{/*订单信息*/
      number:sessionStorage.getItem('number')||1,
      proId:sessionStorage.getItem('proId')||'',
      activityId:sessionStorage.getItem('activityId')||'',
      addressId:sessionStorage.getItem('addressId')||'',
    }
  },
  mutations: {
    ORDERSAVE (state, v) {
      sessionStorage.setItem('number',v.number);
      sessionStorage.setItem('proId',v.proId);
      sessionStorage.setItem('activityId',v.activityId);
      state.orderC.number=v.number;
      state.orderC.proId=v.proId;
      state.orderC.activityId=v.activityId;
    },
    ADDRESSSAVE(state, v){
      sessionStorage.setItem('addressId',v);
      state.orderC.addressId=v;
    },
    ORDERCLEAR(state){
      sessionStorage.removeItem('number');
      sessionStorage.removeItem('proId');
      sessionStorage.removeItem('activityId');
      sessionStorage.removeItem('addressId');
      state.orderC.number='';
      state.orderC.proId='';
      state.orderC.activityId='';
      state.orderC.addressId='';
    }
  },
  actions: {}
});
