import axios from 'axios';
import {Toast} from "cube-ui";
import qs from "qs"

/*请求合并只出现一次loading*/
let needLoadingRequestCount = 0

function showFullScreenLoading() {
  if (needLoadingRequestCount === 0) {
    loading('start')/*loading加载*/
  }
  needLoadingRequestCount++
}

function tryHideFullScreenLoading() {
  if (needLoadingRequestCount <= 0) return
  needLoadingRequestCount--
  if (needLoadingRequestCount === 0) {
    setTimeout(tryCloseLoading,300)/*300ms 间隔内的 loading 合并为一次*/
  }
}
const tryCloseLoading = () => {
  if (needLoadingRequestCount === 0) {
    loading('end')/*loading加载*/
  }
}
/*loading加载*/
function loading (str){
  let toast = Toast.$create({/*滴滴弹框*/
    txt: '',
    mask: true,
    time: 0
  })
  if(str=='start'){
    toast.show()
  }else if(str=='end'){
    toast.hide()
  }
}
/*请求拦截*/
axios.interceptors.request.use(
  config => {
    const token = '123412';
    config.baseURL ='';
    config.timeout = 50000;
    if (config.showLoading) {
      showFullScreenLoading()
    }
    if(config.parsed){
      if(!config.data.username){
        config.data.username=sessionStorage.getItem('userid');
      }
      config.data=qs.stringify(config.data);
    }
    config.headers['TOKEN']=token;
    return config;
  },
  error => {
    return Promise.reject(err);
  }
);
/*请求响应拦截*/
axios.interceptors.response.use(
  response => {
    if(response.config.showLoading){
      tryHideFullScreenLoading()
    }
    return response;
  },
  error => {
    Toast.$create({
      txt: error.message,
      type: 'error',
      time:2000,
      onTimeout: () => {
        if(error.config.showLoading){
          tryHideFullScreenLoading()
        }
      }
    }).show()
    return Promise.reject(error)
  }
)

/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */

export function get(url, config = { showLoading: true,parsed:true }){
  return new Promise((resolve,reject) => {
    axios.get(url,config)
      .then(response => {
        resolve(response.data);
      })
      .catch(err => {
        reject(err)
      })
  })
}

/**
 * 封装post请求
 * @param url
 * @param data
 * @returns {Promise}
 */

export function post(url,data = {}, config = { showLoading: true,parsed:true }){
  return new Promise((resolve,reject) => {
    axios.post(url,data,config)
      .then(response => {
        resolve(response.data);
      })
      .catch(err => {
        reject(err)
      })
  })
}
