import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import VueWechatTitle from "vue-wechat-title";
import "amfe-flexible";
import Cube from "cube-ui";
import axios from "axios";
import {post,get} from "@/assets/js/http"
import "@/assets/css/common.styl"
import VueLazyLoad from 'vue-lazyload'

Vue.config.productionTip = false;
Vue.prototype.$post=post;
Vue.prototype.$get=get;
Vue.prototype.$bee='bee';
Vue.use(Cube);
Vue.use(VueWechatTitle);
Vue.use(VueLazyLoad,{
  error:"https://fx18.oss-cn-hangzhou.aliyuncs.com/bee/1546580772856_200*200.png",
  loading:"https://fx18.oss-cn-hangzhou.aliyuncs.com/bee/1546580708700_200*200.png"
})

router.beforeEach((to, from, next) => {
  let position = window.scrollY
  if (position == null) position = 0
  from.meta.position = position
  next()
});
router.afterEach((to, from, next) => {
  let position = to.meta.position
  if(position>=0){
    Vue.nextTick(() => {
      window.scrollTo(0, position);
    });
  }else{
    window.scrollTo(0, 0);
  }
});

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
